.card-category {
  display: flex;
  align-items: center;
  gap: 5px;
}
.card {
  max-width: 300px;
}
.card-head {
  display: flex;
  flex-direction: column;
  padding: 0px 18px;
  padding-top: 20px;
}
.card-title {
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-top: 5px;
}
.title-head {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.card-rooms {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  top: 15px;
  color: rgba(112, 110, 110, 0.753);
  padding-bottom: 12px;
}
.card-rooms .icons {
  font-size: 15px;
  margin-right: 5px;
}
